import { supabase } from "@/lib/supabase";

export class OnBoardingService {
  async checkOnboardingStatus(userId) {
    try {
      console.log("ðŸ” Checking onboarding status for user:", userId);

      if (!userId) {
        console.error("âŒ No user ID provided");
        return { isOnboarded: false, error: "No user ID provided" };
      }

      const { data, error } = await supabase
        .from("users")
        .select("is_onboarded")
        .eq("user_id", userId)
        .single();

      console.log("ðŸ“Š Supabase query result:", {
        data,
        error,
        hasData: !!data,
        hasError: !!error,
      });

      if (error) {
        // Handle different types of errors
        if (error.code === "PGRST116") {
          console.error("âŒ User not found in users table");
          return { isOnboarded: false, error: "User not found in database" };
        } else {
          console.error("âŒ Supabase error:", error);
          return { isOnboarded: false, error };
        }
      }

      // Check if we got valid data
      if (data && typeof data.is_onboarded === "boolean") {
        console.log("âœ… Onboarding status:", data.is_onboarded);
        return { isOnboarded: data.is_onboarded, error: null };
      } else {
        console.error("âš ï¸ Unexpected data format:", data);
        return { isOnboarded: false, error: "Unexpected data format" };
      }
    } catch (error) {
      console.error("ðŸ’¥ Exception in checkOnboardingStatus:", error);
      return { isOnboarded: false, error };
    }
  }

  async markAsOnboarded(userId) {
    try {
      console.log("ðŸ”§ Marking user as onboarded, user_id:", userId);

      if (!userId) {
        console.error("âŒ No user ID provided");
        return { success: false, error: "No user ID provided" };
      }

      const { data, error } = await supabase
        .from("users")
        .update({ is_onboarded: true })
        .eq("user_id", userId)
        .select()
        .single();

      console.log("ðŸ“Š Mark onboarded result:", { data, error });

      if (error) {
        console.error("âŒ Error marking user as onboarded:", error);
        return { success: false, error };
      }

      console.log("âœ… Successfully marked user as onboarded");
      return { success: true, data, error: null };
    } catch (error) {
      console.error("ðŸ’¥ Exception marking user as onboarded:", error);
      return { success: false, error };
    }
  }

  async getOnboardingProgress(userId) {
    return await this.checkOnboardingStatus(userId);
  }
}

export const onBoardingService = new OnBoardingService();
