import { useState, useCallback } from "react";
import { onBoardingService } from "@/services/onboarding-service";

export const useOnboarding = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [onboardingStatus, setOnboardingStatus] = useState(null);

  const checkOnboardingStatus = useCallback(async (userId) => {
    if (!userId) {
      console.error("âŒ useOnboarding: No user ID provided");
      return { isOnboarded: false, error: "No user ID provided" };
    }

    setLoading(true);
    setError(null);

    try {
      console.log("ðŸ” useOnboarding: Checking status for user:", userId);
      const result = await onBoardingService.checkOnboardingStatus(userId);
      console.log("ðŸ“Š useOnboarding: Service returned:", result);

      if (result.error) {
        console.error("âŒ useOnboarding: Service error:", result.error);
        // Fix: Use the error object directly or extract message properly
        const errorMessage =
          result.error.message || JSON.stringify(result.error);
        setError(errorMessage);
        return { isOnboarded: false, error: result.error };
      }

      console.log("âœ… useOnboarding: Status is:", result.isOnboarded);
      setOnboardingStatus(result.isOnboarded);
      return { isOnboarded: result.isOnboarded, error: null };
    } catch (err) {
      console.error("ðŸ’¥ useOnboarding: Exception:", err);
      const errorMessage = err.message || JSON.stringify(err);
      setError(errorMessage);
      return { isOnboarded: false, error: err };
    } finally {
      setLoading(false);
    }
  }, []);

  const markAsOnboarded = useCallback(async (userId) => {
    if (!userId) {
      console.error(
        "âŒ useOnboarding: No user ID provided for markAsOnboarded"
      );
      return { success: false, error: "No user ID provided" };
    }

    setLoading(true);
    setError(null);

    try {
      console.log("ðŸŽ¯ useOnboarding: Marking user as onboarded:", userId);
      const result = await onBoardingService.markAsOnboarded(userId);
      console.log("âœ… useOnboarding: Mark result:", result);

      if (result.error) {
        console.error("âŒ useOnboarding: Mark error:", result.error);
        const errorMessage =
          result.error.message || JSON.stringify(result.error);
        setError(errorMessage);
        return { success: false, error: result.error };
      }

      setOnboardingStatus(true);
      return { success: true, error: null };
    } catch (err) {
      console.error("ðŸ’¥ useOnboarding: Exception marking onboarded:", err);
      const errorMessage = err.message || JSON.stringify(err);
      setError(errorMessage);
      return { success: false, error: err };
    } finally {
      setLoading(false);
    }
  }, []);

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  return {
    // Methods
    checkOnboardingStatus,
    markAsOnboarded,
    clearError,

    // State
    loading,
    error,
    onboardingStatus,
  };
};
